<template>
	<div>
		<img src="../assets/images/coffee.jpg">
		<div class="AD-div">
			<h1>{{times}}</h1>
			<button @click="pass">跳过</button>
		</div>
	</div>
</template>

<script>
	export default {
		data(){
			return {
				times:10,
				timer:null
			}
		},
		methods:{
			pass(){
				this.$router.push({name:'menu'})
			}
		},
		created(){
			//将底部导航隐藏
			// this.$store.state.isearch = false;
			//倒计时
			this.timer = setInterval(()=>{
				if(this.times == 0){
					this.$router.push({name:'menu'})
				}else{
					this.times--
				}
			},1000)
		},
		beforeDestroy() {
			//关闭定时器
			clearInterval(this.timer)
			this.$store.state.isearch = true;
		  },
	}
</script>

<style lang="less" scoped>
img{width: 100%;height: 100%;}
.AD-div{
	position: absolute;
	top: 20px;
	right: 20px;
	h1{
		font-size: 40px;
		font-weight: bold;
		color: #fff;
		text-align: center;
	}
	button{
		outline: none;
		border: none;
		width: 50px;
		height: 25px;
		border-radius: 15px;
		font-size: 15px;
		text-align: center;
		line-height: 25px;
		background: rgba(109, 108, 108,0.5);
		color: #fff;
	}
}
</style>